<template>
	<div class="editable-list-pro">
		<editableList :editableList="editableList"><slot></slot></editableList>
		<textButton @click.native="addItem(editableList)" class="add-button">{{addButtonText}}</textButton>
	</div>
</template>
<script>
	export default {
		name: 'editableListPro',
		props: {
			editableList: {
				type: Array,
				default: function() {
					return []
				}
			},
			addButtonText: {
				type: String,
				default: '新增一条'
			},
			defaultItem: {
				type: Object
			}
		},
		data() {
			return {
				initDragList: '',
			}
		},
		methods: {
			addItem(arr) {
				arr.push(this.defaultItem)
			}
		},
	}
</script>



