<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定选中项目的 value 值，可以使用 v-model 双向绑定数据</td>
<td>String / Number</td>
<td>-</td>
</tr>
<tr>
<td>title</td>
<td>选择框标题</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>选择框默认文字</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>animationName</td>
<td>弹出窗动画类型 [ <code>scale</code>-缩放式 ]</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<p><code>title</code> 和  <code>placeholder</code> 插入的文字都需要使用 <code>' 单引号 '</code> 包裹传入</p>
