<template>
	<li class="line-item" ref="lineItem">
		<i class="icon-hook"></i><slot></slot>
	</li>
</template>
<script>
	export default {
		name: 'lineItem',
		mounted() {
			var lineItem = this.$refs.lineItem
			
			if ($(lineItem).index()>2) {
				$(lineItem).addClass('hidden')
			}
		}
	}
</script>


