<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;page-section\&quot;&gt;\n  &lt;div class=\&quot;subpage-section\&quot;&gt;\n    &lt;formlist&gt;\n      &lt;ul class=\&quot;row\&quot;&gt;\n        &lt;li&gt;\n          &lt;couple title=\&quot;公司名称\&quot;&gt;深圳泰坦云&lt;/couple&gt;\n        &lt;/li&gt;\n        &lt;li&gt;\n          &lt;uinput title=\&quot;公司名称\&quot;&gt;&lt;/uinput&gt;\n        &lt;/li&gt;\n        &lt;li&gt;\n          &lt;uselect :placeholder=\&quot;'请选择'\&quot; :animationname=\&quot;'scale'\&quot;&gt;\n            &lt;itemlist class=\&quot;popover-up-arrow\&quot;&gt;\n              &lt;li&gt;1&lt;/li&gt;\n              &lt;li&gt;2&lt;/li&gt;\n              &lt;li&gt;3&lt;/li&gt;\n            &lt;/itemlist&gt;\n          &lt;/uselect&gt;\n        &lt;/li&gt;\n      &lt;/ul&gt;\n      &lt;ul class=\&quot;row classs\&quot;&gt;\n        &lt;li class=\&quot;colspan2\&quot;&gt;\n          &lt;checkboxgroup&gt;\n            &lt;ucheckbox val=\&quot;商旅公司\&quot;&gt;商旅公司&lt;/ucheckbox&gt;\n            &lt;ucheckbox val=\&quot;旅行社\&quot;&gt;旅行社&lt;/ucheckbox&gt;\n            &lt;ucheckbox val=\&quot;个人代理\&quot;&gt;个人代理&lt;/ucheckbox&gt;\n            &lt;ucheckbox val=\&quot;商旅公司\&quot;&gt;商旅公司&lt;/ucheckbox&gt;\n            &lt;ucheckbox val=\&quot;旅行社\&quot;&gt;旅行社&lt;/ucheckbox&gt;\n            &lt;ucheckbox val=\&quot;个人代理\&quot;&gt;个人代理&lt;/ucheckbox&gt;\n          &lt;/checkboxgroup&gt;\n        &lt;/li&gt;\n        &lt;li&gt;\n          &lt;radiogroup&gt;\n            &lt;uradio val=\&quot;在线付\&quot;&gt;在线付&lt;/uradio&gt;\n            &lt;uradio val=\&quot;到店付\&quot;&gt;到店付&lt;/uradio&gt;\n          &lt;/radiogroup&gt;\n        &lt;/li&gt;\n      &lt;/ul&gt;\n      &lt;ul class=\&quot;row multiple\&quot;&gt;\n        &lt;li class=\&quot;colspan3\&quot;&gt;习近平总书记1月5日在学习贯彻党的十九大精神研讨班开班式上发表重要讲话，鲜明指出，当前，我国正处于一个大有可为的历史机遇期。这是习近平总书记纵观过去、当下与未来的历史演进，通览国家、政党、民族的沉浮兴衰，作出的重大战略判断，是中国特色社会主义进入新时代的国家表达、民族表达，彰显着当代中国共产党人洞察历史的睿智、创造历史的担当。&lt;/li&gt;\n      &lt;/ul&gt;\n    &lt;/formlist&gt;\n  &lt;/div&gt;\n&lt;/div&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="box-demo-show" slot="component"><div class="page-section">
  <div class="subpage-section">
    <formlist>
      <ul class="row">
        <li>
          <couple title="公司名称">深圳泰坦云</couple>
        </li>
        <li>
          <uinput title="公司名称"></uinput>
        </li>
        <li>
          <uselect :placeholder="'请选择'" :animationname="'scale'">
            <itemlist class="popover-up-arrow">
              <li>1</li>
              <li>2</li>
              <li>3</li>
            </itemlist>
          </uselect>
        </li>
      </ul>
      <ul class="row classs">
        <li class="colspan2">
          <checkboxgroup>
            <ucheckbox val="商旅公司">商旅公司</ucheckbox>
            <ucheckbox val="旅行社">旅行社</ucheckbox>
            <ucheckbox val="个人代理">个人代理</ucheckbox>
            <ucheckbox val="商旅公司">商旅公司</ucheckbox>
            <ucheckbox val="旅行社">旅行社</ucheckbox>
            <ucheckbox val="个人代理">个人代理</ucheckbox>
          </checkboxgroup>
        </li>
        <li>
          <radiogroup>
            <uradio val="在线付">在线付</uradio>
            <uradio val="到店付">到店付</uradio>
          </radiogroup>
        </li>
      </ul>
      <ul class="row multiple">
        <li class="colspan3">习近平总书记1月5日在学习贯彻党的十九大精神研讨班开班式上发表重要讲话，鲜明指出，当前，我国正处于一个大有可为的历史机遇期。这是习近平总书记纵观过去、当下与未来的历史演进，通览国家、政党、民族的沉浮兴衰，作出的重大战略判断，是中国特色社会主义进入新时代的国家表达、民族表达，彰显着当代中国共产党人洞察历史的睿智、创造历史的担当。</li>
      </ul>
    </formlist>
  </div>
</div>
</div>
