export default {
	// bind 第一次绑定元素的时候调用
	bind: function(el, binging) {
		function handleClick(item) {
			if (!item.classList.contains('active')) {
				var curItem = item.siblings('.active');
				curItem[0] && curItem[0].classList.remove('active');
				// item.classList.add('active');
			}
			if (binging.expression) {
				binging.value(item)
			}
		}
		// 在元素上挂载 click方法
		el.handleClick = handleClick;
		//给所有的元素绑定方法
		for (let item of el.children) {
			item.addEventListener('click', ()=>handleClick(item), false)
		}
	},
	// 被绑定元素插入父节点时调用
	inserted: function(el, binging) {
		binging.modifiers.clickFirstItem && el.handleClick(el.children[0])
		//如果binging这个对象的修饰符 modifiers里面有 clickFirstItem , 那么就点击第一个元素
	},

	//binding 一个对象
	//modifiers 包含修饰符的对象
}


