<template>
  <div class="filter-bar clear">
    <div class="filter-list" ref="filterList">
      <slot></slot>
    </div>
    <ul class="filter-btn-list item-group">
      <li class="search"><uButton class="button-small button-fill-primary coat-6" @click="search">搜索</uButton></li>
      <li class="reset coat-10" @click="reset">重置</li>
      <li class="show coat-10" @click="toggle" ref="folded"> {{buttonText}}</li>
    </ul>
  </div>
</template>
<script>
  export default {
    name: "filterBar",
    data() {
      return {
        itemLenght: 0
      }
    },
    props: {
     onSearch: {
      type: Function,
      default() {},
    },
    onReset: {
      type: Function,
      default() {},
    },
  },
  data() {
    return {
      buttonText: "展开"
    };
  },
  methods: {
    search(){
      this.onSearch()
    },
    reset(){
      this.onReset()

    },
    toggle() {
      
      for (let el of this.$refs.filterList.filterChildren('collapsible')) {
        el.classList.toggle('visible');
      }
      if (this.buttonText==='展开') {
        this.buttonText = '收起'
      } else {
        this.buttonText = '展开'
      }
    }
  },
    
  mounted: function() {
    let els = this.$refs.filterList.children;
    for (let el of els) {
      var index = [].indexOf.call(els, el);
      if (index>2) {
        el.classList.add('collapsible')
      }
    }
    if(els.length<4) this.$refs.folded.style.display = "none";
  }
};
</script>



