// import '../docs/scss/main.scss'

import { uInput, doubleInput }  from './components/uInput'
import { uSelect } from './components/uSelect'
import { uCheckbox, checkboxGroup } from './components/uCheckbox'
import { uRadio, radioGroup } from './components/uRadio'
import uSwitch from './components/uSwitch'
import { tabBar, omniTab } from './components/tabBar'
import numSpinner from './components/numSpinner'
import treeMenu from './components/treeMenu'
import { uButton, omniButton } from './components/uButton'
import ripple from './components/ripple'
import segmentBar from './components/segmentBar'
import searchBar from './components/searchBar'
import filterBar from './components/filterBar'
import { editableListPro, editableList, editableItem } from './components/editableList'
import textButton from './components/textButton'
import modal from './components/modal'
import formlist from './components/formlist'
import tablelist from './components/tablelist'
import itemlist from './components/itemlist'
import paddleNav from './components/paddleNav'
import couple from './components/couple'
import hoverPopover from './components/hoverPopover'
import {timeline, lineItem} from './components/timeline'
import toggleBox from './components/toggleBox'
import sliderBar from './components/sliderBar'
import cardBar from './components/cardBar'
import moreButton from './components/moreButton'
import clickPopover from './components/clickPopover'
import {animatedIconUpDownArrow, animatedIconSetup, animatedIconCross} from './components/animatedIcon'



const Fui = {
  uInput,
  uCheckbox,
  checkboxGroup,
  uRadio,
  radioGroup,
  uSwitch,
  tabBar,
  treeMenu,
  uButton,
  ripple,
  segmentBar,
  searchBar,
  filterBar,
  textButton,
  editableList,
  editableItem,
  uSelect,
  modal,
  formlist,
  tablelist,
  itemlist,
  paddleNav,
  couple,
  hoverPopover,
  omniButton,
  omniTab,
  timeline,
  lineItem,
  doubleInput,
  toggleBox,
  numSpinner,
  sliderBar,
  cardBar,
  moreButton,
  clickPopover,
  animatedIconUpDownArrow,
  animatedIconSetup,
  animatedIconCross,
  editableListPro
}
Fui.install = (Vue) => {
  for (const componentName in Fui) {
    const component = Fui[componentName]
    if (component && componentName !== 'install') {
      Vue.component(component.name, component)
    }
  }
}
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}
export default Fui




