<template>
  <clickPopover class="select-ctr" @togglePanel="togglePanel" :animationName="animationName" :type="type">
    <uInput 
    :title="title" 
    :placeholder="placeholder" 
    :readonly="true" ref="input" slot="fuze" >
    <span class="i-downArrow">
      <animatedIconUpDownArrow :isAnimated="visible"></animatedIconUpDownArrow>
    </span>
  </uInput>
  <slot>
    
  </slot>
</clickPopover>
</template>
<script>
  export default {
    name: "uSelect",
    props: {
     title: {
      type: String,
    },
    placeholder: {
      type: String,
      default: '请选择...'
    },
    type: {
      type: String
    },
    animationName: {
      type: String
    }
  },
  data() {
    return {
      visible: false
    }
  },
  methods: {
    togglePanel(val) {
      this.visible = val;
      console.log(val,'select')
    }
  }    
};
</script>




