<template>
	<div class="popover" @mouseover="togglePanel" @mouseleave="togglePanel">
		<div class="fuze"><slot name="fuze"></slot></div>
		<transition :name="popoverAnimationName">
			<div class="popover-content-container" v-show="visible">
				<slot></slot>
			</div>
		</transition>
	</div>
</template>
<script>
	export default {
		name: 'hoverPopover',
		props: {
			animationName: {
				type: String
			}
		},
		data() {
			return {
				visible: false,
			}
		},
		computed: {
			popoverAnimationName: function() {
				return `popover-${this.animationName}`
			}
		},
		methods: {
			togglePanel(e) {
				var vue = this,
				popList = this.$el,
				popListClassList = popList.classList;
				if (e.type==='mouseover') {
					popListClassList.add('hover');
				} else if (e.type === 'mouseleave') {
					popListClassList.remove('hover');
				}
				setTimeout(function() {
					vue.visible = popListClassList.contains('hover') ? true : false
				}, 150)
			},
			hidePanel() {
				this.visible = false;
			}
		}
	}
</script>



