<template>
	<ul class="timeline" :class="{ packed: isPacked }">
		<slot></slot>
		<li class="showmore clickable" @click="showmore" v-if="isPacked" ref="showmore">查看更多</li>
	</ul>
</template>
<script>
	export default {
		name: 'timeline',
		props: {
			isPacked: {
				type: Boolean,
				default: false
			}
		},
		methods: {
			showmore() {
				var showmore = this.$refs.showmore;
				for (let item of showmore.siblings('.hidden')) {
					item.style.display = "block"
				}
				showmore.style.display = "none";
			}
		}
	}
</script>



