        "html": "<div class=\"page-section\">\n  <div class=\"subpage-section\">\n    <formlist>\n      <ul class=\"row\">\n        <li>\n          <couple title=\"公司名称\">深圳泰坦云</couple>\n        </li>\n        <li>\n          <uinput title=\"公司名称\"></uinput>\n        </li>\n        <li>\n          <uselect :placeholder=\"'请选择'\" :animationname=\"'scale'\">\n            <itemlist class=\"popover-up-arrow\">\n              <li>1</li>\n              <li>2</li>\n              <li>3</li>\n            </itemlist>\n          </uselect>\n        </li>\n      </ul>\n      <ul class=\"row classs\">\n        <li class=\"colspan2\">\n          <checkboxgroup>\n            <ucheckbox val=\"商旅公司\">商旅公司</ucheckbox>\n            <ucheckbox val=\"旅行社\">旅行社</ucheckbox>\n            <ucheckbox val=\"个人代理\">个人代理</ucheckbox>\n            <ucheckbox val=\"商旅公司\">商旅公司</ucheckbox>\n            <ucheckbox val=\"旅行社\">旅行社</ucheckbox>\n            <ucheckbox val=\"个人代理\">个人代理</ucheckbox>\n          </checkboxgroup>\n        </li>\n        <li>\n          <radiogroup>\n            <uradio val=\"在线付\">在线付</uradio>\n            <uradio val=\"到店付\">到店付</uradio>\n          </radiogroup>\n        </li>\n      </ul>\n      <ul class=\"row multiple\">\n        <li class=\"colspan3\">习近平总书记1月5日在学习贯彻党的十九大精神研讨班开班式上发表重要讲话，鲜明指出，当前，我国正处于一个大有可为的历史机遇期。这是习近平总书记纵观过去、当下与未来的历史演进，通览国家、政党、民族的沉浮兴衰，作出的重大战略判断，是中国特色社会主义进入新时代的国家表达、民族表达，彰显着当代中国共产党人洞察历史的睿智、创造历史的担当。</li>\n      </ul>\n    </formlist>\n  </div>\n</div>\n",
