<template>
	<div class="tab-bar">
		<ul class="tab-list" ref="tabList"  v-clickList.clickFirstItem="clickTab">
			<slot></slot>
		</ul>
		<div class="accessary">
			<slot name="accessary"></slot>
		</div>
	</div>
</template>
<script>
    import clickList from 'src/directives/clickList'
	export default {
		name: 'tabBar',
		props: {
			callBack: {
				type: Function,
				default() {},
			}
		},
		methods: {
			clickTab(tab) {
				this.callBack && this.callBack(tab);
			}
		},
		directives: { clickList },
	}
</script>



