<template>
	<ul class="itemlist" v-clickList="clickItem">
		<slot></slot>
	</ul>
</template>
<script>
	import clickList from 'src/directives/clickList'
	export default {
		name: 'itemlist',
		directives: { clickList },
		methods: {
			clickItem() {
				if (this.$parent.$options.name.indexOf('Popover') !== -1) {
					this.$parent.hidePanel()
				}
			}
		}
	}
</script>



