<template>
	<div class="formlist" >
		<slot></slot>
	</div>
</template>
<script>
	export default {
		name: 'formlist',
		methods: {
			tabClick(tab) {
				if (!tab.classList.contains('active')) {
					var curTab = tab.siblings('.active');
					curTab[0] && curTab[0].classList.remove('active');
					tab.classList.add('active');
				}
			}
		},
		mounted: function() {
			var formRowList = this.$el.children,
			vue = this;
			for (let row of formRowList) {
				for ( let item of row.children) {
					item.addEventListener('mousedown', () => vue.tabClick(item), false)
				}
			}
		},
	}
</script>


