<template>
	<div class="editable-list">
		<slot></slot>
	</div>
</template>
<script>
	export default {
		name: 'editableList',
		props: {
			editableList: {
				type: Array,
				default: function() {
					return []
				}
			}
		},
		data() {
			return {
				initDragList: '',
				dragList: '',
			}
		},
		
		mounted() {
			var vue = this;
			this.dragList = this.$el.children;
			this.initDragList = function(dragList) {
				
				var marginTop = dragList[1].getStyle('marginTop').replace(/px/g, '');
				var item_height = dragList[0].offsetHeight + parseInt(marginTop);
				var dragedObjArr = [];

				for (var item of dragList) {
					var itemObj = {};
					var index = [].indexOf.call(dragList, item);
					itemObj.el = item;
					itemObj.index = index;
					itemObj.tl = new TimelineMax();
					itemObj.curPosition = 0;
					dragedObjArr.push(itemObj);
				};
                

				dragedObjArr.forEach(function(dragedObj) {

					let [dragEl, accumDeltaY, originIndex, insertedIndex ] = [dragedObj.el, 0, 0, 0];

					var shuffleList = function(deltaY) {
						
						// Func accumulates the accumDeltaY in every drag which generates a deltaY.
						// When the accumuDeltaY is equal to or greater than the shuffle shrehold, 
						// func triggles new shuffle.
						accumDeltaY = accumDeltaY + deltaY;
						const shredhold = item_height;
						if (Math.abs(accumDeltaY)>=shredhold) {

							var curIndex = dragedObjArr.indexOf(dragedObj);
							var curPosition = dragedObj.curPosition;
							var isDragedDown = accumDeltaY>0 ? true : false;

                            // Normally, user moves mouse in normal drag speed, detalY will be less than item_height, and accumDeltaY will be less than 2 times item_height, so in this case, shuffledItem will  be either the item next to draged object or the item prev to the draged object, but in some rare cases, the drag speed is fast enough to push the detalY to outnumber 2 times item_height, even to be multiple times item_height, then dragList will generate a shuffleList which lenght is the Math.floor(Math.abs(accumDeltaY)/(item_height)).
							var shuffledList_len = ~~(Math.abs(accumDeltaY)/(item_height));
							var voidIndex = 
							isDragedDown ? curIndex+shuffledList_len: curIndex-shuffledList_len;
							var ShuffleStartIndex = isDragedDown ? curIndex+1 : voidIndex;
							var shuffleEndIndex = isDragedDown ? voidIndex+1 : curIndex;
							var shuffledList = dragedObjArr.slice(ShuffleStartIndex, shuffleEndIndex);
							insertedIndex = voidIndex;
  
                            // After every shuffle, 
                            // func updates the dragedObjArr's order,
                            // simutaniously, updatas dragedObj's position.
                            // The remainder of accumDeltaY divided item_height will be assign to accumDeltaY as new deltaY accumulation's origin value. 
							dragedObjArr.remove(dragedObj);
							dragedObjArr.splice(voidIndex, 0, dragedObj);
							if (isDragedDown) {
								dragedObj.curPosition += item_height*shuffledList_len
							} else {
								dragedObj.curPosition -= item_height*shuffledList_len
							}
							accumDeltaY = accumDeltaY%item_height;

                            for (let shuffledObj of shuffledList) {
                            	if (isDragedDown) {
									shuffledObj.curPosition -= item_height
								} else {
									shuffledObj.curPosition += item_height
								}
								shuffledObj.tl.to(shuffledObj.el, 0.2, {
									y: isDragedDown ? `-=${item_height}` : `+=${item_height}`,
									onComplete: function() {
										this.kill();
									}
								});
                            }
						}
					};

					dragEl.addEventListener('mousedown', function() {
						dragEl.classList.add('active');
						if (dragEl.siblings('.active').len>0) {
							dragEl.siblings('.active')[0].classList.remove('active')
						}       
					});

					dragEl.addEventListener('mouseup', function() {
						dragEl.classList.remove('active')
					});

					var drag = new Draggable(dragEl, {
						type: 'y',
						bounds: '.editable-list',
						edgeResistance: 0.96,
						onDragStart: function() {
							originIndex = dragedObj.index;
						},
						onDrag: function() {
							shuffleList(this.deltaY);
						},
						onDragEnd: function() {

							// after drag, force the draggedObj to snap to list grid.
							var curPosition = dragedObj.curPosition;
							dragEl.classList.remove('active')
							dragedObj.tl.to(dragedObj.el, 0.2, {
								y: curPosition,
								onComplete: function() {
									accumDeltaY = 0;
									vue.$emit('shuffleList', [originIndex, insertedIndex]);
								}
							});
						}
					})
				});
			}
			this.initDragList(this.dragList);
		},
		updated() {
			this.initDragList(this.dragList);
		}
	}
</script>



