<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
<td>String</td>
<td>null</td>
</tr>
<tr>
<td>wrap</td>
<td>包裹层 [ 可选: <code>wrap-20</code>,<code>wrap-14</code>,<code>wrap-10</code>,<code>wrap-4</code> ]</td>
<td>String</td>
<td>null</td>
</tr>
</tbody>
</table>
<!-- | title    | 按钮文字   | String | 空
| disabled    | 是否禁用   | Boolean | false
| outlined | 是否显示边框| Boolean | false
| loading | 设置按钮为加载中状态   | Boolean |  false       |
| active | 设置按钮为激活状态  | Boolean |  false       |
| showicon | 是否显示图标  | Boolean |  false       |
| icon |  对应字体图标的class   | String |  无       | -->
