<template>
	<div class="animated-icon-container">
		<svg class="animated-icon-down-up-arrow" viewBox="0 -28 47 80">
			<path id="down-arrow" d="M1 0 L23 23 45 0" ref="downArrow"></path>
			<path id="up-arrow" d="M1 23 L23 0 45 23" ref="upArrow"></path>
		</svg>
	</div>
	
</template>
<script>
	export default { 
		name: "animatedIconUpDownArrow",
		props: {
			isAnimated: {
				type: Boolean
			}
		},
		data() {
			return {
				tl: '',
			}
		},
		watch: {
			isAnimated(val) {
				console.log(val,'animate---0-----')
				val ? this.tl.play() : this.tl.reverse(true);
			}
		},
		mounted() {
			this.tl = new TimelineMax({paused: true});
			this.tl.to(this.$refs.downArrow, 0.25, { morphSVG: this.$refs.upArrow});
		}
	}
</script>


