<demo-box :jsfiddle="{&quot;html&quot;:&quot;\n&lt;div class=\&quot;md-box\&quot;&gt;\n  &lt;hoverpopover&gt;\n    &lt;div slot=\&quot;fuze\&quot; class=\&quot;room-type\&quot;&gt;高级大床房&lt;/div&gt;\n    &lt;itemlist class=\&quot;popover-down-arrow\&quot;&gt;\n      &lt;li&gt;海景高级大床房&lt;/li&gt;\n      &lt;li&gt;高级行政房&lt;/li&gt;\n      &lt;li&gt;总统套房&lt;/li&gt;\n    &lt;/itemlist&gt;\n  &lt;/hoverpopover&gt;\n\n  &lt;hoverpopover class=\&quot;ml30\&quot;&gt;\n    &lt;div slot=\&quot;fuze\&quot; class=\&quot;room-type\&quot;&gt;高级大床房&lt;/div&gt;\n    &lt;itemlist class=\&quot;popover-up-arrow\&quot;&gt;\n      &lt;li&gt;海景高级大床房&lt;/li&gt;\n      &lt;li&gt;高级行政房&lt;/li&gt;\n      &lt;li&gt;总统套房&lt;/li&gt;\n    &lt;/itemlist&gt;\n  &lt;/hoverpopover&gt;\n&lt;/div&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="box-demo-show" slot="component">
<div class="md-box">
  <hoverpopover>
    <div slot="fuze" class="room-type">高级大床房</div>
    <itemlist class="popover-down-arrow">
      <li>海景高级大床房</li>
      <li>高级行政房</li>
      <li>总统套房</li>
    </itemlist>
  </hoverpopover>

  <hoverpopover class="ml30">
    <div slot="fuze" class="room-type">高级大床房</div>
    <itemlist class="popover-up-arrow">
      <li>海景高级大床房</li>
      <li>高级行政房</li>
      <li>总统套房</li>
    </itemlist>
  </hoverpopover>
</div>

</div>
