<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>提示触发方式: <code>clickPopover</code> 以及 <code>hoverPopover</code></td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>class</td>
<td>提示出现方位 <code>popover-down-arrow</code> [出现在上面] 以及 <code>popover-up-arrow</code> [出现在下面]</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="events"><a class="header-anchor" href="#events" aria-hidden="true">¶</a> Events</h3>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定选中项目的 value 值，可以使用 <code>v-model</code> 绑定获取值</td>
<td>-</td>
</tr>
