<template>
	<div class="animated-icon-container">
		<svg class="animated-icon-cross svg-stroke" viewBox="0 0 30 30">
			<path ref="leftLine" d="M4 -4 L4 26"></path>
			<path ref="rightLine" d="M26 -4 L26 26"></path>
		</svg>
	</div>
</template>
<script>
	export default { 
		name: "animatedIconCross",
		props: {
			isAnimated: {
				type: Boolean
			}
		},
		data() {
			return {
				tl: '',
			}
		},
		watch: {
			isAnimated(val) {
				val ? this.tl.play() : this.tl.reverse(true);
			}
		},
		mounted() {
			this.tl = new TimelineMax({paused: true, reversed: true});
			this.tl.to(this.$refs.leftLine, 0.1, { rotation: 45, transformOrigin: '0 100%', opacity: 1})
			.to(this.$refs.rightLine, 0.1, {rotation: -45, transformOrigin: '0 100%', opacity: 1}, '-=0.1')
		}
	}
</script>


