<template>
	<dl class="couple">
		<dt class="couple-title" v-if="title" ref="title" :class="{ 'auto-width': autoWidth }"><span>{{title}}</span></dt>
		<dd class="couple-content" :class="classes" ref="content"><slot></slot></dd>
	</dl>
</template>
<script>
	export default {
		name: 'couple',
		props: {
			title: {
				type: String,
				default: ''
			},
			wrap: {
				type: String,
				default: '',
			},
			autoWidth: {
				type: Boolean,
			},
			ellipsis: {
				type: Boolean,
				default: false
			}
		},
		computed: {
			classes() {
				return [`${this.wrap}`, { ellipsis: this.ellipsis}]
			}
		},
		mounted() {
			if (this.$refs.title) {
				var w = this.$el.clientWidth-this.$refs.title.clientWidth-32;
				this.$refs.content.style.width = `${w}px`
			}
		}
	}
</script>



