<template><section><h2 id="ucheckbox-fu-xuan-kuang"><a class="header-anchor" href="#ucheckbox-fu-xuan-kuang" aria-hidden="true">¶</a> uCheckbox 复选框</h2>
<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<h4 id="ucheckbox"><a class="header-anchor" href="#ucheckbox" aria-hidden="true">¶</a> uCheckbox</h4>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>绑定的原生uCheckbox的name</td>
<td>String</td>
<td>无</td>
</tr>
<tr>
<td>val</td>
<td>uCheckbox的value值</td>
<td>String, Number, Boolean</td>
<td>无</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用 (<code>样式冲突,暂不可用</code>)</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>checked</td>
<td>是否已选</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>change</td>
<td>值发生变化时的回调</td>
<td>Function</td>
<td>无</td>
</tr>
</tbody>
</table>
<h4 id="ucheckbox-group"><a class="header-anchor" href="#ucheckbox-group" aria-hidden="true">¶</a> uCheckbox-group</h4>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定选中的选项</td>
<td>Array</td>
<td>无</td>
</tr>
<tr>
<td>change</td>
<td>值变化时的回调</td>
<td>Function</td>
<td>无</td>
</tr>
</tbody>
</table>
<p>uCheckbox 组件调用方式 <code>&lt;uCheckbox type=&quot;primary&quot; checked&gt;&lt;/uCheckbox&gt;</code></p>
<h3 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h3>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;ucheckbox val=\&quot;john\&quot; :checked=\&quot;true\&quot;&gt;uCheckbox&lt;/ucheckbox&gt;\n\n&lt;br&gt;&lt;br&gt;\n&lt;ucheckbox v-model=\&quot;checkedNames\&quot; val=\&quot;Jackie\&quot; checked&gt;Jackie Chan&lt;/ucheckbox&gt;\n{{ checkedNames }}\n\n&lt;br&gt;&lt;br&gt;\n&lt;ucheckbox label=\&quot;uCheckbox\&quot; val=\&quot;john\&quot; checked disabled&gt;uCheckbox&lt;/ucheckbox&gt;\n\n&lt;br&gt;&lt;br&gt;\n&lt;ucheckbox label=\&quot;uCheckbox\&quot; val=\&quot;john\&quot; :on-change=\&quot;changeFunc\&quot;&gt;uCheckbox111&lt;/ucheckbox&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        checkedNames: '',\n      };\n    },\n    methods: {\n\n    },\n  };\n&quot;,&quot;style&quot;:null}">
                    <div class="box-demo-show" slot="component"><ucheckbox val="john" :checked="true">uCheckbox</ucheckbox>

<br><br>
<ucheckbox v-model="checkedNames" val="Jackie" checked>Jackie Chan</ucheckbox>
{{ checkedNames }}

<br><br>
<ucheckbox label="uCheckbox" val="john" checked disabled>uCheckbox</ucheckbox>

<br><br>
<ucheckbox label="uCheckbox" val="john" :on-change="changeFunc">uCheckbox111</ucheckbox>



</div>
