<template>
	<div class="u-input" @mouseover="over()" @mouseleave="leave()" ref="inputCtr">
		<!-- 标题 -->
		<template v-if="regex == 'mobile'">
			<input
			v-if="!type || type === 'text'"
			class="input"
			:autocomplete="autocomplete"
			:autocapitalize="autocapitalize"
			:autocorrect="autocorrect"
			:spellcheck="spellcheck"
			type="text"
			:name="name"
			pattern="[0-9]*"
			:placeholder="placeholder"
			:readonly="readonly"
			:disabled="disabled"
			@focus="focusHandler"
			:value="currentValue"
			@keyup="handleKeyup"
			@blur="onBlur"
			@keyup.enter="onKeyUp"
			@input="handleInput"
			ref="input"/>
		</template>
		<template v-else>
			<input
			v-if="!type || type === 'text'"
			:maxlength="max"
			:autocomplete="autocomplete"
			:autocapitalize="autocapitalize"
			:autocorrect="autocorrect"
			:spellcheck="spellcheck"
			:disabled="disabled"
			:placeholder="placeholder"
			:readonly="readonly"
			type="text"

			:style="{paddingRight: (suffix.strWidth())}"
			:name="name"
			:pattern="pattern"
			:value="currentValue"

			@focus="focusHandler"
			@keyup="handleKeyup"
			@blur="onBlur"
			@keyup.enter="onKeyUp"
			@input="handleInput"
			ref="input"/>
			<input
			v-if="type === 'password'"
			class="input"
			:maxlength="max"
			:autocomplete="autocomplete"
			:autocapitalize="autocapitalize"
			:autocorrect="autocorrect"
			:spellcheck="spellcheck"
			:style="inputStyle"
			type="password"
			:name="name"
			:value="currentValue"
			:pattern="pattern"
			:placeholder="placeholder"
			:readonly="readonly"
			:disabled="disabled"
			@focus="focusHandler"
			@blur="onBlur"
			@keyup.enter="onKeyUp"
			@input="handleInput"
			ref="input"/>

			<input
			v-if="type === 'number'"
			class="input"
			:min="min"
			:max="max"
			:autocomplete="autocomplete"
			:autocapitalize="autocapitalize"
			:autocorrect="autocorrect"
			:spellcheck="spellcheck"
			:style="inputStyle"
			type="number"
			:name="name"
			:value="currentValue"
			:pattern="pattern"
			:placeholder="placeholder"
			:readonly="readonly"
			:disabled="disabled"
			@focus="focusHandler"
			@blur="onBlur"
			@keyup.enter="onKeyUp"
			@input="handleInput"
			ref="input"/>

		</template>

		<span class="title" ref="title" v-if="title">{{title}}</span>
   <span class="suffix" v-if="suffix">
    {{suffix}}
  </span>
  <!-- 清除按钮 -->

  

  <slot>
    <span class="clear-button"  @click="clear" :style="{paddingRight: suffix.strWidth()}"
    >
      <animatedIconCross :isAnimated="showClearIcon"></animatedIconCross>
    </span>
  </slot>

  <!-- 浮动placeholder -->
  <label for="text" v-show="showplaceholder" v-if="isIe==true">{{placeholder}}</label>

  <!-- 错误提示 -->
  <span class="tip" 
  v-if="showErrorIcon" v-show="(!!regex || !!min || !!max || required) && iserror && initError">{{errorCode}}</span>
</div>

</template>
<script>
  export default {
    name: "uInput",
    created() {
      this.currentValue = this.value || "";
      if (!this.title && !this.placeholder && !this.currentValue) {
      }
    var ie = navigator.appVersion.indexOf("MSIE") != -1; // 是否IE
    if (ie) {
      this.isIe = true;
    }
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    suffix: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "text"
    },
    readonly: Boolean,
    showErrorIcon: {
      type: Boolean,
      default: false
    },
    required: {
      type: Boolean,
      default: false
    },
    autocomplete: {
      type: String,
      default: "off"
    },
    autocapitalize: {
      type: String,
      default: "off"
    },
    autocorrect: {
      type: String,
      default: "off"
    },
    value: {
      type: [String, Number],
      default: ""
    },
    perrorMsg: {
      type: String,
      default: "请输入正确字符串内容"
    },
    placeholder: {
      type: String,
      default: "请输入..."
    },
    spellcheck: {
      type: Boolean
    },
    maxlength: {
      type: Number
    },
    regex: String,
    disabled: {
      type: Boolean,
      default: false
    },
    min: Number,
    max: Number,
    readonly: {
      type: Boolean,
      default: false
    },
    name: {
      type: String
    },
    number: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      hasRestrictedLabel: false,
      firstError: "",
      showplaceholder: true,
      forceShowError: false,
      hasLengthEqual: false,
      valid: true,
      iserror: false,
      isIe: false,
      currentValue: this.value,
      showWarn: true,
      initError: false,
      valid: true,
      errorMsg: this.perrorMsg,
      errorCode: "",
      isempty: !this.value,
      showClearIcon: true,
      titleWidth: 0,
      regexObj: {
        email:
        "^\\w+((-\\w+)|(\\.\\w+))*\\@[A-Za-z0-9]+((\\.|-)[A-Za-z0-9]+)*\\.[A-Za-z0-9]+$",
        mobile: "^(86)?1[3,4,5,7,8]\\d{9}$",
        bankcard: "^\\d{15,19}$"
      }
    };
  },
  computed: {
    pattern() {
      if (this.keyboard === "number" || this.isType === "china-mobile") {
        return "[0-9]*";
      }
    }
  },
  methods: {
    clear() {
      this.currentValue = "";
    },
    focus() {
      this.$refs.input.focus();
    },
    blur() {
      this.$refs.input.blur();
    },
    focusHandler() {
      this.$emit("on-focus", this.currentValue);
      this.showplaceholder = false;
      this.showClearIcon = true;
    },
    onBlur() {
      this.$emit("on-blur", this.currentValue);
      this.showClearIcon = false;
      if (this.currentValue.length < 1) {
        this.showplaceholder = true;
      }
      this.validatorInput(this.currentValue, true);
    },
    handleKeyup(event) {
      this.$emit("on-keyup", event);
    },
    onKeyUp(e) {
      if (e.keyCode == 13) {
        e.target.blur();
        this.$emit("on-enter", this.currentValue);
      }
    },
    validatorInput(val, showError) {
      this.initError = showError;
      if (showError) this.showWarn = false;
      if (this.required && val === "") {
        this.setError("不能为空", "NOT_NULL");
        this.iserror = true;
        return;
      }
      if (this.min && val.length < this.min) {
        this.setError(`最少输入${this.min}位字符`, "NOT_MIN_SIZE");
        this.iserror = true;
        return;
      }
      const v = this.regex === "bankcard" ? val.replace(/\s/g, "") : val;
      const reg = this.regexObj[this.regex]
      ? this.regexObj[this.regex]
      : this.trim(this.regex, "/");
      if (!!v && this.regex && !new RegExp(reg).test(v)) {
        this.setError(this.errorMsg, "NOT_REGEX_RULE");
        this.iserror = true;
        return;
      }
      this.iserror = false;
      this.valid = true;
      this.errorMsg = "";
      this.errorCode = "";
    },
    handleInput(event) {
      let value = event.target.value;
      this.$emit("input", value);
      this.$emit("on-change", event);
    },
    setError(error, code) {
      this.errorMsg = error;
      this.errorCode = code;
      this.valid = false;
    },
    trim(str, char) {
      if (!!str) {
        return str.replace(
          new RegExp("^\\" + char + "+|\\" + char + "+$", "g"),
          ""
          );
      }
      return str;
    },
    over() {
      if (this.currentValue.length > 1) {
        this.showClearIcon = true;
      }
    },
    leave() {
      this.showClearIcon = false;
    }
  },
  watch: {
    value(val) {
      this.currentValue = val;
    },
    currentValue(val) {
      this.isempty = !val;
    }
  },
  mounted() {
    if (this.$refs.title) {
      this.$refs.input.style.paddingLeft = `${(this.$refs.title.innerText.strWidth()+1.2)}em`;
    }
    this.validatorInput(this.currentValue, false);
    var hasHeading = document.querySelector(".money");
    if (hasHeading != false && hasHeading != null) {
      hasHeading.nextElementSibling.style.right = "40px";
    }
  }
};
</script>




