<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>按钮类型 [ <code>fill</code> - 填充按钮 / <code>stroke</code> - 空心按钮 / <code>plain</code> - 空白按钮 ]</td>
<td>String</td>
<td>null</td>
</tr>
<tr>
<td>color</td>
<td>颜色类型 [ <code>primary</code>-默认颜色 / <code>complement</code>- XX颜色 / <code>dark</code>-深色 / <code>light</code>-亮色 / <code>warning</code>-警告色 ]</td>
<td>String</td>
<td>null</td>
</tr>
<tr>
<td>class-size</td>
<td>如需要小型按钮 在组件上添加 <code>button-small</code> 类名</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<p>组件调用方式 :  <code>&lt;uButton class=&quot;button-fill-primary&quot;&gt;按钮&lt;/uButton&gt;</code>
触发事件在外部进行操作和设置</p>


