import Vue from 'vue';
import VueRouter from 'vue-router';
import Fui from '../src/index';
import App from './App';
import routes from './router.conf';
import '../src/docscss/main.scss'
import './scss/main.scss'


// 自定义指令
import clickList from '../src/directives/clickList'
import coupleEllipsis from '../src/directives/coupleEllipsis'


//import axios from 'axios';
import VueResource from 'vue-resource'
Vue.use(Fui, { prefix: 'Fui' });
Vue.use(VueRouter);

import DemoBox from './components/DemoBox';

//axios.defaults.baseURL = 'http://www.fangcang.com';
const router = new VueRouter({
  routes,
  linkActiveClass: 'is-active',
});

String.prototype.strWidth = function() {
	var width = (this) ? (this.replace(/[^x00-xff]/g, '00').length / 2 + 1) : 0
	return width-0.5 + 'em';
}


// Vue.filter('multiplyBy', function(value) {
// 	return '×' + value  
// })
Vue.directive('clickList', clickList);
Vue.directive('coupleEllipsis', coupleEllipsis);


// 释放demo的组件
Vue.component('demo-box', DemoBox);

router.beforeEach((to, from, next) => {
  next();
});
new Vue({// eslint-disable-line
  router,
  render: h => h(App),
}).$mount('#app');



