<template>
	<div class="animated-icon-container">
		<svg class="animated-icon-setup svg-fill" viewBox="0 0 30 30">
			<use xlink:href="#i-setup"></use>
		</svg>
	</div>
</template>
<script>
	export default { 
		name: "animatedIconSetup",
		props: {
			isAnimated: {
				type: Boolean
			}
		},
		data() {
			return {
				tl: '',
			}
		},
		watch: {
			isAnimated(val) {
				val ? this.tl.play() : this.tl.reverse(true);
			}
		},
		mounted() {
			this.tl = new TimelineMax({paused: true, reversed: true});
			this.tl.to(this.$el, 0.5, { rotation: 67.5})
		}
	}
</script>


