<template>
	<div class="paddle-nav">
		<transition name="fade">
			<i class="paddle-nav-arrow paddle-nav-arrow-previous icon-leftArrow" 
			@click="previousIndex()" v-show="index>1"></i>
		</transition>
		<transition name="fade">
			<i class="paddle-nav-arrow paddle-nav-arrow-next icon-rightArrow" 
			@click="nextIndex()" v-show="index<length"></i>
		</transition>
		
	</div>
</template>
<script>
	export default {
		name: 'paddleNav',
		props: {
			length: {
				type: Number
			},
		},
		data() {
			return {
				index: 1
			}
		},
		methods: {
			previousIndex() {
				if (this.index !== 1) {
					this.index--;
					this.$emit('previousIndex')
				}
			},
			nextIndex() {

				if (this.index < this.length) {
					this.index++;
					this.$emit('nextIndex')
				}
			}
		}
	}
</script>


