<template>
	<div class="more-button" @click="toggle" ref="moreButton">
		<ul ref="dotList">
			<slot></slot>
			<li v-if="hasClose" class="close"><div>×</div></li>
		</ul>
	</div>
</template>
<script>
	export default {
		name: 'moreButton',
		props: {
			hasClose: {
				type: Boolean,
				default: true
			},
			span: {
				type: Number,
				default: 45
			},
			autoFold: {
				type: Boolean,
				default: true
			},
			fixedAnchor: {
				type: Boolean,
				default: false
			}
		},
		data() {
			return {
				moreButton: '',
				MoreButton: ''
			}
		},
		methods: {
			toggle(event) {
				event.preventDefault();
                event.stopPropagation();

				if (this.autoFold) {
					this.$emit('animate');
					this.moreButton.tl.reversed() ? this.moreButton.tl.play() : this.moreButton.tl.reverse();
				} else {
					if (this.moreButton.tl.reversed()) {
						this.$emit('animate');
						this.moreButton.tl.play()
					} else if (event.target.parentNode.classList.contains('close')) {
						this.$emit('animate');
						this.moreButton.tl.reverse()
					}
				}
			}
		},
		mounted() {

			
			this.MoreButton = function(el, timeScale, span, fixedAnchor) {
				var dotList = el.children.toArray();
				var dotList_l = dotList.length,
				moreButton_w = 20,

				dot_w = dotList[0].offsetWidth,
				dotOffsetXlist = [],
				buttonList = [];

				
				span<0 && dotList.reverse();
				var dotList_x = document.querySelector('.dotList')
				dotList.forEach(function(dot) {
					var buttonIndex = dotList.indexOf(dot);
					var factor = dotList_l-buttonIndex-1;
					var x = -factor*(span);
					var ax = moreButton_w-dot_w;

					if (!fixedAnchor)  x = (factor<dotList_l-2)?x-ax/2:(factor==dotList_l-1?x+ax/2:x)

					
					dotOffsetXlist.push(x);
					buttonList.push(dot.childNodes);
				})

				var mainTl = new TimelineMax({paused: true,reversed: true})
				var tl0 = new TimelineMax(),
				tl1 = new TimelineMax(),
				ease = Power2.easeInOut;
				tl0.staggerTo(dotList, 0.6, {
					cycle: { x: dotOffsetXlist },
					x: -span*2,
					backgroundColor: 'currentColor',
					ease: ease
				}, 0.05)

				tl1.staggerFromTo(buttonList, 0.3, {
					scale: 0,
					opacity: 0,
					ease: ease,
					display: 'none',
				}, {
					scale: 1,
					opacity: 1,
					ease: ease,
					display: 'inline'
				}, 0.05)
				.delay(-0.6)

				mainTl
				.add(tl0)
				.add(tl1)
				.timeScale(timeScale)
				this.dotList = dotList;
				this.tl = mainTl;
			}
			this.moreButton = new this.MoreButton(this.$el.children[0], 1.2, this.span, this.fixedAnchor);
			
		},
		updated() {
			this.moreButton = new this.MoreButton(this.$refs.dotList, 1.2, this.span, this.fixedAnchor);
		}
	}
</script>





