<template>
	<tabBar class="slider-bar" :callBack="setSlider">
		<slot></slot>
		<div class="slider"></div>
		<template slot="accessary"><slot name="accessary"></slot></template>
	</tabBar>
</template>

<script>
	export default {
		name: 'sliderBar',
		data() {
			return {
				setSlider: function(tab) {
					var slider = this.$el.querySelector('.slider');
					TweenMax.to(slider, 0.3, { width: tab.offsetWidth, x: tab.offsetLeft })
				} 
			}
		}
	}
</script>



