<template>
	<div class="popover" v-clickoutside="hidePanel">
		<div class="fuze"  @click="togglePanel"><slot name="fuze"></slot></div>
		<transition :name="popoverAnimationName">
			<div class="popover-content-container" v-if="visible" @click="togglePanel">
				<slot></slot>
			</div>
		</transition>
	</div>
</template>
<script>
	import clickoutside from 'src/directives/clickoutside'
	export default {
		name: 'clickPopover',
		props: {
			animationName: {
				type: String
			}
		},
		computed: {
			popoverAnimationName: function() {
				return `popover-${this.animationName}`
			}
		},
		data() {
			return {
				visible: false,
			}
		},
		// created:(){

		// },
		methods: {
			togglePanel () {
				console.log('togglePanel');
				this.visible = !this.visible;
				this.$emit('togglePanel', this.visible)
			},
			hidePanel() {
				console.log('hidePanel');
				this.visible = false;
				this.$emit('togglePanel', this.visible)
			}
		},
		directives: { clickoutside }
	}
</script>



