<template>
	<div class="editable-item">
		<div class="editable-item-content"><slot></slot></div>
		
		<div class="delete-button-container">
			<transition name="editable-list-slide">
				<div v-show="editableList.length>1" class="delete-button" @click="deleteItem(editableItem, editableList)">
					<i class="icon-trash"></i>
				</div>
			</transition>
		</div>
		
	</div>
</template>
<script>

	export default {
		name: 'editableItem',
		props: {
			editableList: {
				type: Array,
				default: function() {
					return []
				}
			},
			editableItem: {
				type: Object
			}
		},
		methods: {
			deleteItem(item, arr) {
				arr.remove(item)
			}
		}
	}
</script>





