<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<h4 id="radio"><a class="header-anchor" href="#radio" aria-hidden="true">¶</a> radio</h4>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>绑定的原生radio的name</td>
<td>String</td>
<td>无</td>
</tr>
<tr>
<td>val</td>
<td>radio的value值</td>
<td>String, Number, Boolean</td>
<td>无</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>checked</td>
<td>是否已选</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>on-change</td>
<td>值变化时的回调</td>
<td>Function</td>
<td>无</td>
</tr>
<tr>
<td>tstyle</td>
<td>样式class</td>
<td>String</td>
<td>blu-radio</td>
</tr>
</tbody>
</table>
