<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>输入框类型，可选值为 <code>text</code> , <code>password</code> , <code>number</code></td>
<td>String</td>
<td>test</td>
</tr>
<tr>
<td>value</td>
<td>绑定的值，可使用 v-model 双向绑定</td>
<td>String / Number</td>
<td>空</td>
</tr>
<tr>
<td>readonly</td>
<td>设置输入框为只读</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>maxlength</td>
<td>最大输入长度</td>
<td>Number</td>
<td>-</td>
</tr>
</tbody>
</table>
<!-- | title    | 按钮文字   | String | 空
| disabled    | 是否禁用   | Boolean | false
| outlined | 是否显示边框| Boolean | false
| loading | 设置按钮为加载中状态   | Boolean |  false       |
| active | 设置按钮为激活状态  | Boolean |  false       |
| showicon | 是否显示图标  | Boolean |  false       |
| icon |  对应字体图标的class   | String |  无       | -->
