<template>
	<div class="toggle-box">
		<div class="toggle">
			<slot name="toggle">
				<uSwitch @toggle="toggle"></uSwitch>
			</slot>
		</div>
		<div class="content" v-show="isVisible">
			<slot></slot>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				isVisible: false
			}
		},
		name: 'toggleBox',
		methods: {
			toggle() {
				this.isVisible = !this.isVisible
			}
		}

	}
</script>



