<template>
  <tabBar class="segment-bar">
   <slot></slot> 
   <template slot="accessary"><slot name="accessary"></slot></template>
 </tabBar>
</template>
<script>
  export default {
    name: "segmentBar",
    props: {
      hasConfig: {
        type: Boolean,
        default: false
      },
      equalWidth: {
        type: Boolean,
        default: true
      }
    },
    methods: {
      setSegmentWidth(segmentBar) {
        let [maxWidth, segmentList] = [0, segmentBar.children]

        for (let segment of segmentList) {
          let item_width = segment.offsetWidth;
          maxWidth = item_width>maxWidth ? item_width : maxWidth;
        }

        for (let segment of segmentList) {
          segment.style.width = `${maxWidth}px`;
        }
      }
    },
    mounted: function() {
      this.equalWidth && this.setSegmentWidth(this.$el.querySelector('.tab-list'));
    }
  };
</script>



